.profile_card {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    gap: 2px;
    padding: 35px;
    box-sizing: border-box; /* Includes padding and border in the element's total width and height */
    background-color: var(--t0);
    border: var(--bw) solid var(--bc);
    border-radius: var(--br);
    backdrop-filter: blur(5px); /* Applies the blur effect */
    -webkit-backdrop-filter: blur(5px); /* Safari compatibility */
    box-shadow: var(--sh);
}

.profile_card h3 {
    margin-bottom: 40px;
    color: var(--t2);
}

.profile_card .social-icons {
    display: flex;
    flex-wrap: wrap;
    gap: 60px;
    padding-bottom: 100px;

}

.profile_card .icon {
    font-size: 35px;
}

/* Media query for smaller screens */
@media (max-width: 600px) {
    .profile_card .social-icons {
        gap: 40px;
        width: 150px;
        padding-bottom: 50px;
    }
}
