.numbers{
    margin-top: 50px;
    color: var(--t3);
    display: flex;
    gap: 50px;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    align-items: top;
    padding: 10px;
}

.numbers .pair{
    display: flex;
    gap: 50px;
}

.numbers .box{
    width: 190px;
}

@media (max-width: 750px) {
    .numbers .box {
        width: 120px;
    }
}